<template>
  <!-- <YPage :logo="require('@/assets/img/logo_white.png')">
    <div class="yu-page-container s-tool-wrapper">
      颜色
    </div>
  </YPage> -->
  <div class="yu-page-container s-tool-wrapper">
    <div class="triangle-hd">
      <div class="triangle-hd-item">
        <h1>方向</h1>
        <div class="direction-wrapper">
          <div class="direction-item direction-top-left"></div>
          <div class="direction-item direction-top-center"></div>
          <div class="direction-item direction-top-right"></div>
          <div class="direction-item direction-mid-left"></div>
          <div class="direction-item direction-mid-center"></div>
          <div class="direction-item direction-top-right"></div>
          <div class="direction-item direction-bottom-left"></div>
          <div class="direction-item direction-bottom-center"></div>
          <div class="direction-item direction-bottom-right"></div>
        </div>
      </div>
      <div class="triangle-hd-item">
        <h1>尺寸</h1>
        <div class="direction-wrapper">
          <div class="direction-item"></div>
        </div>
      </div>
      <div class="triangle-hd-item">
        <h1>颜色</h1>
        <div class="color-wrapper">
          <div class="color-hd yu-flex-wrap">
            <span :style="{backgroundColor:item}" v-for="(item,index) in colors" :key="index" v-copy="item"></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import YPage from '@/components/YPage.vue'

export default {
  components: {
    YPage
  },
  data() {
    return {
      colors: ["#000000", "#ff6b6b", "#f06595", "#cc5de8", "#845ef7", "#5c7cfa", "#339af0", "#22b8cf", "#20c997", "#51cf66", "#94d82d", "#fcc419"]
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
.s-tool-wrapper {
  .triangle-hd {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;

    .triangle-hd-item {
      -webkit-flex: 1;
      -ms-flex: 1;
      -webkit-box-flex: 1;
      -moz-box-flex: 1;
      flex: 1;

      h1 {
        font-size: 16px;
        padding: 10px 0;
        color: #666;
      }

      .direction-wrapper {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-wrap: wrap;

        .direction-item {
          position: relative;
          width: 60px;
          height: 60px;
          border: 1px solid #fff;
          border-radius: 5px;
        }

        .direction-item::after {
          content: '';
          display: inline-block;
          width: 12px;
          height: 12px;
          border-top: 2px solid #999;
          border-right: 2px solid #999;
          vertical-align: middle;
          position: absolute;
          top: 50%;
          left: 50%;
        }

        .direction-top-left::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }

        .direction-top-center::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }

        .direction-top-right::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }
        .direction-mid-left::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }
        .direction-mid-center::after {
          display: none;
        }
        .direction-mid-right::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }
        .direction-bottom-left::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }

        .direction-bottom-center::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }

        .direction-bottom-right::after {
          transform: rotate(45deg);
          margin-left: -7px;
          margin-top: -7px;
        }
      }

      .color-wrapper {
        .color-hd {
          gap: 6px;

          span {
            width: 18px;
            height: 18px;
            border-radius: 5px;
            opacity: 0.8;
            transition: all .15s ease;
            cursor: pointer;

            &:hover {
              -webkit-transform: scale(1.1);
              transform: scale(1.1);
              opacity: 1;
            }
          }
        }
      }
    }
  }
}

</style>
